.about-us
    display: flex
    flex-direction: column
    margin-top: 82px
    &:nth-child(odd)
        .about-us-body
            flex-direction: row-reverse
            justify-content: space-between
        .about-us-text
            margin-left: auto
        .about-us-avatar
            margin-right: auto
        
.about-us-body
    display: flex
    flex-wrap: wrap
    margin-top: 92px
    justify-content: space-between

.about-us-text
    flex-basis: 50%

.about-us-lists
    max-width: 534px
    padding-left: 26px
    margin-top: 29px
    font-weight: normal
    font-size: 24px
    line-height: 28px

.about-us-avatar
    flex-basis: 50%
    max-width: 443px
    max-height: 514px

.about-us-description
    flex-basis: 100%
    display: flex
    flex-wrap: wrap
    justify-content: space-between
.description-body
    width: 540px
    margin-top: 47px
    font-weight: normal
    font-size: 24px
    line-height: 28px

.sub-info-lists
    display: flex
    align-items: center
    max-width: 380px

.about-us-sub-info
    display: flex
    justify-content: space-between
    margin-top: 126px

.sub-info-text
    padding-left: 4px
    font-weight: normal
    font-size: 24px
    line-height: 28px

.sub-info-round
    min-width: 60px
    min-height: 60px
    background: #7FFFD4
    border-radius: 50%
    display: flex
    justify-content: center
    align-items: center
    color: #fff

